<template>
  <div class="container">
    <!-- <div class="row">
          <div class="col-md-12 my-3">
              <h2>Room</h2>
              <input v-model="roomId">
          </div>
      </div> -->

    <div class="row" style="display: none">
      <div class="col-md-12">
        <vue-webrtc ref="webrtc" width="100%" :roomId="roomId" :socketURL="'https://t-skyline-387315.de.r.appspot.com'"
          :enableVideo="false" v-on:joined-room="joined" v-on:left-room="left" v-on:opened-room="opened"
          v-on:share-started="shareOn" v-on:share-stopped="shareOff" @error="onError" />
      </div>
    </div>

    <div class="row">
      <div class="col-md-12 center">
        <div v-show="call && people > 1" style="font-size: 26px">
          {{ timer }}
        </div>
      </div>
    </div>

    <div class="row my-4">
      <div class="col-md-12 center" style="font-size: 26px">
        <div v-show="call && people > 1">
          Talking to customer service...
          <br />
          与服务人员通话中...
        </div>

        <div v-show="call && people === 1">
          Wait for the customer service staff to answer...
          <br />
          等待客服人员接听
        </div>

        <div v-show="!call">
          Click "Start Call" to contact customer service
          <br />
          点击"开始通话"与客服联系
        </div>
      </div>
    </div>

    <div class="row footer">
      <div class="col-md-4" style="font-size: 26px">
        <img class="btn" :src="loudspeakerOn" style="width: 100%" />
        Speaker
        <br />
        免提
      </div>
      <div class="col-md-4" style="font-size: 26px">
        <img :src="callOff" class="btn" style="width: 100%" @click="onLeave" />
        End Call
        <br />
        结束通话
      </div>
      <div class="col-md-4" style="font-size: 26px">
        <img :src="callOn" class="btn" style="width: 100%" @click="onJoin" />
        Start Call
        <br />
        开始通话
        <!-- <button type="button" class="btn btn-primary" @click="onCapture">Capture Photo</button>
                      <button type="button" class="btn btn-primary" @click="onShareScreen">Share Screen</button> -->
      </div>
    </div>
    <!-- <div class="row">
          <div class="col-md-12">
              <h2>Captured Image</h2>
              <figure class="figure">
                  <img :src="img" class="img-responsive" />
              </figure>
          </div>
      </div> -->
  </div>
</template>

<script>
import { VueWebRTC } from 'vue-webrtc';
import callOn from '../assets/call-on.png';
import callOff from '../assets/call-off.png';
import loudspeakerOn from '../assets/loudspeaker-on.png';
import loudspeakerOff from '../assets/loudspeaker-off.png';
import keepCommunication from '../assets/door_of_memories.mp3'

export default {
  name: 'demo-component',
  components: {
    'vue-webrtc': VueWebRTC
  },
  data() {
    return {
      roomId: '',
      img: null,
      call: false,
      callOn,
      callOff,
      loudspeakerOn,
      loudspeakerOff,
      time: 0,
      timerID: null,
      people: 0,
      keep: new Audio(keepCommunication),
      servicesStaff: true,
    };
  },
  mounted() {
    this.keep.loop = true;
    this.roomId = this._uuid();
    window.onbeforeunload = () => {
      this.$refs.webrtc.leave();
    }
  },
  computed: {
    timer() {
      let hours = Math.floor(this.time / 3600);
      let minutes = Math.floor((this.time - hours * 3600) / 60);
      let seconds = this.time - hours * 3600 - minutes * 60;

      if (hours < 10) {
        hours = '0' + hours;
      }
      if (minutes < 10) {
        minutes = '0' + minutes;
      }
      if (seconds < 10) {
        seconds = '0' + seconds;
      }

      return `${hours}:${minutes}:${seconds}`;
    }
  },
  watch: {
    people: {
      handler: function (e) {
        if (e === 1) {
          this.keep.play()
        } else {
          this.keep.pause();
          this.keep.currentTime = 0;
        }
      },
    }
  },
  methods: {
    onJoin() {
      this.call = true;
      this.$refs.webrtc.join();
    },
    onLeave() {
      this.call = false;
      this.$refs.webrtc.leave();
      this.timerID = null;
      this.time = 0;
      this.people = 0;
      clearInterval(this.timerID);
    },
    onShareScreen() {
      this.img = this.$refs.webrtc.shareScreen();
    },
    onError(error, stream) {
      console.log('On Error Event', error, stream);
    },
    joined(e) {
      console.log('Someone Joined Event', e);
      this.people++;

      if (!this.timerID && this.people > 1) {
        this.timerID = setInterval(() => {
          this.time++;
        }, 1000);
      }
    },
    left(e) {
      console.log('Someone Left Event', e);
      this.people--;
    },
    opened(event) {
      console.log('Opend Event : ', event);
    },
    shareOn() {
      console.log('Share Started Event : ', event);
    },
    shareOff(event) {
      console.log('Share Stoped Event : ', event);
    },
    _uuid() {
      var d = Date.now();
      if (typeof performance !== 'undefined' && typeof performance.now === 'function') {
        d += performance.now(); //use high-precision timer if available
      }
      return 'xxxxxxxx-xxxx-4xxx-yxxx-xxxxxxxxxxxx'.replace(/[xy]/g, function (c) {
        var r = (d + Math.random() * 16) % 16 | 0;
        d = Math.floor(d / 16);
        return (c === 'x' ? r : (r & 0x3 | 0x8)).toString(16);
      });
    }
  }
};
</script>

<style>
.footer {
  position: absolute;
  bottom: 0;
  left: 0;
}
</style>