<template>
    <div class="container">
        <vue-webrtc ref="webrtc" style="display: none" :roomId="roomId" :socketURL="'https://t-skyline-387315.de.r.appspot.com'"
            :enableVideo="false" v-on:joined-room="joined" v-on:left-room="left" v-on:opened-room="opened" />
        <div class="row">
            <div class="col-6">
                <button type="button" class="btn btn-primary mb-4" @click="getRooms">列表重整</button>
                <div v-for="item in rooms" :key="item" class="dropdown mb-1">
                    <button class="btn btn-secondary dropdown-toggle" type="button" data-toggle="dropdown"
                        aria-expanded="false">
                        {{ item }}
                    </button>
                    <div class="dropdown-menu">
                        <a class="dropdown-item" @click="join(item)">接通</a>
                    </div>
                </div>
            </div>
            <div class="col-6">
                <div v-show="roomId">
                    與 {{ roomId }} 通話中
                    <button type="button" class="btn btn-danger" @click="leave">掛斷</button>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
import { VueWebRTC } from 'vue-webrtc';
import axios from 'axios'

export default {
    name: 'control-panel',
    components: {
        'vue-webrtc': VueWebRTC
    },
    data() {
        return {
            roomId: '',
            rooms: [],
            people: 0,
            time: 0,
            timerID: 0
        }
    },
    mounted() {
        this.getRooms();

        window.onbeforeunload = () => {
            this.$refs.webrtc.leave();
        }
    },
    computed: {
        timer() {
            let hours = Math.floor(this.time / 3600);
            let minutes = Math.floor((this.time - hours * 3600) / 60);
            let seconds = this.time - hours * 3600 - minutes * 60;

            if (hours < 10) {
                hours = '0' + hours;
            }
            if (minutes < 10) {
                minutes = '0' + minutes;
            }
            if (seconds < 10) {
                seconds = '0' + seconds;
            }

            return `${hours}:${minutes}:${seconds}`;
        }
    },
    methods: {
        join(roomId) {
            this.roomId = roomId;
            this.$refs.webrtc.join();
        },
        leave() {
            this.$refs.webrtc.leave()
            this.roomId = '';
            this.timerID = null;
            this.time = 0;
            this.people = 0;
        },
        getRooms() {
            axios.get('https://t-skyline-387315.de.r.appspot.com/list').then((response) => {
                this.rooms = response.data
            })
        },
        joined(e) {
            console.log('Someone Joined Event', e);
            this.people++;

            if (!this.timerID && this.people > 1) {
                this.timerID = setInterval(() => {
                    this.time++;
                }, 1000);
            }
        },
        left(e) {
            console.log('Someone Left Event', e);
            this.people--;
        },
        opened(event) {
            console.log('Opend Event : ', event);
        },
    }
}
</script>